	import {debounce} from '../../../common/debounce.js'
	export default {
		name: "DetailGoodsInfo",
    props: {
      detailInfo: {
        type: Object
      }
    },
    data(){
      return{
        counter:0,
        imageLength:0,
		load:true
      }
    },
	mounted() {
		this.imageLoad = debounce(this.imageLoad,50)
	},
    methods:{
      imageLoad(){
		  // setTimeout(()=>{
			  this.$emit('imageLoad',this.load)
		  // },1000)
      }
    },
    watch:{
        detailInfo(){
          this.imageLength = this.detailInfo.detailImage[0].list.length
        }
      }
	}
